.spinner__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.spinner {
  display: inline-block;
  width: 40px;
  height: 40px;
}

.spinner::after {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  margin: 4px;
  border-radius: 50%;
  border: 5px solid red;
  border-color: red transparent yellow transparent;
  animation: spinner 1.2s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
